.danmu .danmu-screen {
  display: block;
  background-color: #000;
  position: relative;
  overflow: hidden;
}
.danmu .danmu-message {
  position: absolute;
  color: #fff;
  transition: linear all;
  line-height: 1em;
  opacity: 0.9;
}
.danmu .danmu-message.ready {
  transform: translateX(640px);
}
.danmu .danmu-message.playing {
  transform: translate(-100%);
}
.danmu .danmu-message.over {
  display: none;
}



